<template>
<el-form ref="form" :model="form" label-width="80px">
  <el-form-item label="" >
    <el-input v-model="form.name" style="width:360px;"></el-input>
  </el-form-item>
  <el-form-item label="">
      <el-select v-model="form.type" placeholder="" style="width:360px;">
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
          <el-option label="" value="3"></el-option>
      </el-select>
  </el-form-item>
  <el-form-item label="">
    <el-upload style="width:360px;"
    class="avatar-uploader"
    drag
    :on-success="setFilename"
    action="/file">
    <i class="el-icon-upload"></i>
  <div class="el-upload__text"><em></em></div>
  <div class="el-upload__tip" slot="tip">20M</div>
    </el-upload>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit"></el-button>
  </el-form-item>
</el-form>
</template>
<script>
  export default {
    data() {
      return {
        form: {
          name: '',
          type: '',
          filename:''
        }
      }
    },
    methods: {
      onSubmit() {
        this.$axios({
          method:'post',
          url:'/component',
          data:this.form
        })
        .then(response => {
          
        })
      },
      setFilename(response,file,file_list){
        this.form.filename = response.data;
      }
    }
  }
</script>

<style>

</style>


