<template>
<div id="register-form">
  <el-form ref="form" :model="form">
    <el-form-item>
      <el-input v-model="form.username" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-input v-model="form.password" type="password" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-input v-model="form.password_confirm" type="password" placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" v-on:click="register" style="width: 100%;text-align: center;"></el-button>
      <div style="width: 100%;text-align: center;">or</div>
      <a style="width: 100%;text-align: center;" href="#/login"></a>
    </el-form-item>
  </el-form>
</div>
</template>
<script type="text/javascript">
  export default {
    data() {
      return {
        form: {
          username: '',
          password: '',
          password_confirm:''
        },
        dom:{
          loginForm:'register-form'
        }
      }
    },
    methods: {
      vetical:function(elId){
        var screen_height   = document.documentElement.clientHeight;
        var screen_width    = document.documentElement.clientWidth;
        var el              = document.getElementById(elId);
        var el_height       = el.clientHeight;
        var el_width        = el.clientWidth;
        console.log(screen_height,screen_width,el_height,el_width);
        el.style.marginTop  = (screen_height - el_height)/2 + 'px';
        el.style.marginLeft = (screen_width - el_width)/2 + 'px';
      },
      register:function(){
        this.$axios({
          method:'post',
          url:'user',
          data:this.form
        })
        .then(response => {
          if(response.data.code == '200'){
            var type = 'success';
          }else{
            var type = 'error';
          }
          this.$message({
            message: response.data.message,
            type: type
          });
        });
      },
      toLogin:function(){
        window.location.herf='#/login';
      }
    },
    mounted:function(){       //
      var self = this;
      self.vetical(self.dom.loginForm);
      window.onresize = function(){
          self.vetical(self.dom.loginForm);
       }
    }
  }
</script>
<style type="text/css">
  body{
    background-color: #242c39;
  }
  #register-form{
    width: 360px;
    /*height: 200px;*/
    background-color: #fff;
    padding: 45px 30px 10px 30px;
    border-radius:5px;
  }
</style>



