<template>
  <transition name="key-value-table">
    <el-table
        :data="data"
        style="width: 100%">
        <el-table-column
          label=""
          width="300">
          <template slot-scope="scope">
            <el-input placeholder="" v-model="scope.row.key"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="300">
          <template slot-scope="scope">
            <el-input placeholder="" v-model="scope.row.value"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-input placeholder="" v-model="scope.row.detail"
              v-on:mouseover.native="showdel(scope.$index)"
              v-on:mouseout.native ="hiddendel(scope.$index)">
              <template slot="append"><el-button slot="append" icon="el-icon-delete" v-on:click.native="removeParam(scope.$index)" v-if="scope.row.showdel"></el-button></template>
            </el-input>
          </template>
        </el-table-column>
    </el-table>
  </transition>
</template>
<script>
  export default {
    props: {
      data: Array,
    },
    data() {
      return {
          /* data:[{
            key:'',
            value:'',
            detail:'',
            showdel:false
          }] */
      }
    },
    methods: {
      showdel:function(index){      //
        if(index != this.data.length-1) this.data[index].showdel = true;
      },
      hiddendel:function(index){    //
        if(index != this.data.length-1) this.data[index].showdel = false;
      },
      removeParam:function(index){    //
        this.data.splice(index,1);
      },
      jsonData:function(){
        let info = {};
        for (let i = 0; i < this.data.length; i++) {
          const param = this.data[i];
          info[param.key] = param.value;
        }
        return info;
      },
      urlData:function(){
        let info = '';
        for (let i = 0; i < this.data.length; i++) {
          const param = this.data[i];
          info += param.key + '=' + param.value + '&';
        }
        return info.substr(0,info.length-1);
      }
    },
    watch:{
      data:{    //
        handler(val,oldVal){
          var info = val[val.length-1];
          if(info.key || info.value || info.detail) this.data.push({key:'',value:'',detail:'',showdel:false});
        },
        deep:true
      }
    }
}
</script>


